import React from 'react'
import './Icon.css'

const icon = (props) => {
    
    return (
        <div className="Icon">
            <a href={props.href} target="_blank">
                <img id="icon" src={require(`../../assets/icons/${props.icon}`)} />
            </a>
        </div>

    )
}

export default icon


